/* First Tab */
.klick-slt-data {
    display: inline-block;
    width: 20%;
    float: left;
    background: #F68D22;
    padding: 20px;
    margin: 0 20px 0 0;
    font-size: 15px;
    font-weight: 600;
    line-height: 1.5;
}
.klick-slt-data h2 {
    color: #F68D22;
    display: inline-block;
    margin-top: 0;
    width: 100%;
    background: #034378;
    padding: 9px;
    box-sizing: border-box;
}

/* Tab wrapper  */
.klick-slt-tab-wrap .nav-tab-wrapper {
 margin: 14px 0px; 
 border-bottom: 1px solid #ccc;
}
.info{
	color: #0073aa;
	font-weight: bold;
}

#message_lbl{
	color: #F5821F;
}
#klick_slt_table_url{
	color : #1F4F7B;
	font-weight:  bold;
	font-size: 25px;
}

#klick_slt_nav_tab_wrapper .dashicons{
    color: #F5821F;
}
/*Log Listing Third tab*/
.klick-slt-log-listing{
	display: block;
}
.klick-slt-log-listing *{
	box-sizing: border-box;
}
.klick-slt-log-listing > h1{
	border-radius: 3px 3px 0 0;
	background: #fff;
	position: relative;
	border-bottom: 1px solid #dfdfdf;
	height: 38px;    	
	margin: 0;
    padding: 0px 10px;
    font-weight: bold;
    font-size: 13px;
    line-height: 38px;
    color: #333;
    text-shadow: 0px 1px 0px #fff;
    text-align: left;
    width: 100%;
    display: inline-block;
}
.klick-slt-log-listing-wrap{
	width: 100%;
	display: inline-block;
	background: #fff;
	padding: 15px;
	max-height: 500px;
	overflow: auto;
}
.klick-slt-log-listing-row{
	border: 1px solid #dedede;
	margin-top: 20px;
}
.klick-slt-log-listing-row:first-child{
	margin-top: 0;
}
.klick-slt-log-listing-row h1{
    margin: 0;
    padding: 0 0px;
    border-bottom: 1px solid #dedede;
    background: #f2f2f2;
    font-weight: normal;
    font-size: 14px;
    line-height: 34px;
    color: #333;
}
.klick-slt-log-listing-row h1 time{
    float: right;
    font-size: 12px;
    color: #555;
}
.klick-slt-log-listing-row h4{
	font-size: 11.5px;
    margin: 20px 0 -5px 10px;
    color: #444;
    text-transform: uppercase;
}
.klick-slt-log-listing-row ul{
	list-style: square;
    padding: 0 0 0 15px;
	margin: 10px 0 0 15px;
	color: #777;
}

/*Plugin List Second tab*/
.klick-slt-plugin-list{
	width: 100%;
	display: inline-block;
	vertical-align: top;
	box-sizing: border-box;
}
.klick-slt-plugin-list *{
	box-sizing: border-box;
}
.klick-slt-plugin-list li{
    width: auto;
    float: left;
    background: #F1F1F1;
    padding: 15px;
    border: 1px solid #CCCCCC;
    margin: 0 10px 20px;
    text-align: center;
}
.klick-slt-plugin-list li h4{
	margin: 5px 0 0 0;
}

/*Custom Button*/
.wp-core-ui .button.klick_btn,.wp-core-ui .button.klick_btn:hover,.wp-core-ui .button.klick_btn:active,.wp-core-ui .button.klick_btn:focus{
    font-size: 14px!important;
    height: 46px!important;
    line-height: 44px!important;
    padding: 0 36px!important;
    background: #F5821F!important;
    color: #fff!important;
    border-color: #f4780b #f4780b #c36009!important;
    -webkit-box-shadow: 0 1px 0 #f4780b !important;
	box-shadow: 0 1px 0 #f4780b !important;
    text-shadow: 0 -1px 1px #f4780b,1px 0 1px #f4780b,0 1px 1px #f4780b,-1px 0 1px #f4780b!important;
}
.wp-core-ui .button.klick_btn:disabled,.wp-core-ui .button.klick_btn[disabled]{
    opacity: 0.5;
}

/* First 3 tabs */
.klick-slt-data-listing-wrap{
    width: 98%;
    display: inline-block;
    background: #fff;
    padding: 15px;
    max-height: 500px;
    overflow: auto;
}
.klick-slt-data-listing-row{
    border: 1px solid #dedede;
    margin-top: 20px;
}
.klick-slt-data-listing-row:first-child{
    margin-top: 0;
}
.klick-slt-data-listing-row h1{
    margin: 0;
    padding: 0 0px;
    border-bottom: 1px solid #dedede;
    background: #f2f2f2;
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: #333;
    min-height: 30px;
}
.klick-slt-info{
    padding-left: 5px;
}
/* ajax loader */
.klick-slt-data-container{
    width: 100%;
    display: inline-block;
    position: relative;

}
.klick-slt-overlay {
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 999;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: none;
    text-align: center;
}
.loading-image{
    position: absolute;
    top: 0;
    margin: auto;
    bottom: 0;
    left: 0;
    right: 0;
}

.klick-slt-data-box{
    width: 100%;
    box-sizing: border-box;
    display: inline-block;
    background: #fff;
    padding: 15px;
    position: relative;
}
.klick-slt-data-table h1{
    margin: 10px 0 30px;
    vertical-align: top;
    display: inline-block;
}
.klick-slt-data-table .klick-slt-add-btn{
    float: right;
}
.klick-slt-data-table table.widefat{
    border: 0;
}
.klick-slt-data-table table tbody {
    display:block;
    max-height:500px;
    overflow:auto;
}
.klick-slt-data-table table thead,.klick-slt-data-table table tbody tr {
    display:table;
    width:100%;
    table-layout:fixed;
}
.klick-slt-data-table .klick-slt-edit-row span,.klick-slt-data-table .klick-slt-delete-row span{
    padding: 5px 3px;
    cursor: pointer;
    -webkit-transition: all 0.35s ease 0s;
       -moz-transition: all 0.35s ease 0s;
        -ms-transition: all 0.35s ease 0s;
         -o-transition: all 0.35s ease 0s;
            transition: all 0.35s ease 0s;
}
.klick-slt-data-table .klick-slt-edit-row span:hover{
    color: #0073aa;
}
.klick-slt-data-table .klick-slt-delete-row span:hover{
    color: red;
}
.klick-slt-data-table table tbody tr td .slt-table-content{
    height: 150px;
    overflow: auto;
    min-height: 20px;
}
.klick-slt-data-table table thead th{
    font-weight: 500;
    color: #fff!important;
    background: #063B6D;
}
#klick_slt_add_link ul{
    padding: 0;
    margin: 0;
}
#klick_slt_add_link ul li{
    margin-bottom: 15px;
}
#klick_slt_add_link ul li label{
    width: 100px;
    float: left;
    margin-right: 15px;
    font-weight: 500;
}
#klick_slt_add_link ul li .slt-list-label-content{
    display: block;
    overflow: hidden;
    max-width: 700px;
}
@media screen and (max-width: 1440px){
    .klick-slt-data-table table tbody{
        max-height: 300px;
    }
}
@media screen and (max-width: 782px){
    .klick-slt-data-table table thead {
        border: none;
        clip: rect(0 0 0 0);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
    }
    .klick-slt-data-table .wp-list-table tbody tr:not(.inline-edit-row):not(.no-items) td:not(.column-primary){
        padding-left: 130px;
        min-height: 20px;
        text-align: left!important;
    }
    .klick-slt-data-table .wp-list-table tbody tr:not(.inline-edit-row):not(.no-items) td:not(.column-primary)::before {
        content: attr(data-label);
        float: left;
        font-weight: bold;
        text-transform: uppercase;
        width: 110px!important;
        text-align: left;
    }
}
@media screen and (max-width: 500px){
    #klick_slt_add_link ul li{
        margin-bottom: 5px;
    }
    #klick_slt_add_link ul li label{
        width: 100%;
        margin: 0 0 4px;
    }
    #klick_slt_add_link ul li .slt-list-label-content{
        width: 100%;
        display: inline-block;
    }
    #klick_slt_add_link ul li .slt-list-label-content input,#klick_slt_add_link ul li .slt-list-label-content textarea{
        width: 99%;
        box-sizing: border-box;
    }
}

.chart-container{
    height: 300px;
}

.download-loader {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.slt-overlay{
    display: none;
}

.slt-list-table .klick-slt-row-container{
    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 2px;
    padding: 10px;
    display: inline-block;
    width: 100%;
    box-sizing: border-box;
    margin-top: 10px;
    line-height: 1.6;
    font-size: 16px;
}
.slt-list-table .klick-slt-row-container .slt-overlay{
    position: relative;
}
.slt-list-table .klick-slt-row-container > span.klick-slt-delete-row{
    float: left;
}
.slt-list-table .klick-slt-row-container > span > a{
    text-decoration: none;
    color: #F5821F;
    font-weight: 500;
}
.slt-list-table .klick-slt-row-container .details-link{
    width: 25%;
    padding-right: 30px;
    vertical-align: top;
    display: inline-block;
}
.slt-list-table .klick-slt-row-container .details-link:empty{
    display: none;
}
.klick-slt-row-left{
    float: left;
    width: 66%;
}
.klick-slt-row-right{
    float: right;
    width: 33%;
}
.klick-slt-row-widget{
    border: 1px solid rgba(0, 0, 0,0.1);
    width: 100%;
    box-sizing: border-box;
    float: left;
    padding: 15px;
    margin: 0 0 15px;
}
.klick-slt-row-widget h4{
    margin: 0 0 15px;
}
.klick-slt-row-widget-50{
    margin: 0 7.5px 15px;   
    width: calc(50% - 15px);
}
.klick-slt-row{
    display: block;
    margin: 0 -7.5px;
}

.klick-our-other-logo img{
    max-width: 100%;
}
.klick-slt-title{
    background: #114474;
    color: #fff;
    width: 100%;
    margin-top: 15px;
    display: inline-block;
}
.klick-slt-title div{
    width: 25%;
    display: inline-block;
    padding: 10px 20px 10px 10px;
    font-weight: 500;
}
.klick-slt-title div:first-child{
    padding-left: 60px;
}

.klick-slt-log-listing-row h1{
    margin: 0;
    padding: 0 0px;
    border-bottom: 1px solid #dedede;
    background: #f2f2f2;
    font-weight: normal;
    font-size: 14px;
    line-height: 34px;
    color: #333;
}


@media screen and (max-width: 767px){
    .klick-slt-title{
        display: none;
    }
    .slt-list-table .klick-slt-row-container .details-link{
        width: 100%;
        padding-right: 0;
    }
}
